*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.portfolio-list, .buttons, .clients-list, .team-link, .team-list, .business-list, .qualities-list, .form .form-list, .footer-section-2 .foot-link, .page-address .address-list, .page-nav .nav-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.form-list .form-textarea:focus-within, .form-list .form-input:focus-within, .form-list .form-item:focus-within {
  border-color: #2196f3;
  outline: transparent;
  fill: #2196f3;
}

.container {
  width: 1200px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}

body {
  background-color: #ffffff;
  color: #212121;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  letter-spacing: 0.03em;
}

/* header */
.header-def .container {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #ececec;
}
.header-def .link {
  display: flex;
  align-items: center;
  padding-top: 32px;
  padding-bottom: 32px;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.14;
  letter-spacing: 0.02em;
  text-decoration: none;
  color: #212121;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.header-def .link:hover, .header-def .link:focus {
  color: #2196f3;
}
.header-def .item {
  position: relative;
}

.page-nav {
  display: flex;
  align-items: center;
}
.page-nav .nav-list {
  display: flex;
  margin-left: 93px;
}
.page-nav .item:not(:first-child) {
  margin-left: 50px;
}
.page-nav .studio,
.page-nav .portfolio,
.page-nav .contacts {
  color: #2196f3;
}
.page-nav .studio::after,
.page-nav .portfolio::after,
.page-nav .contacts::after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 4px;
  border-radius: 2px;
  background-color: #2196f3;
}

.page-address {
  margin-left: auto;
}
.page-address .address-list {
  display: flex;
  align-items: center;
}
.page-address .icon {
  margin-right: 10px;
  fill: currentColor;
}
.page-address .item:last-child {
