.studio-hero {
  position: relative;
  background-color: #c4c4c4;
  background-image: linear-gradient(to right, #2f303a66, #2f303a66), url(../images/backgrnd.jpg);
  background-size: cover;
  color: #ffffff;
  text-align: center;
  max-width: 1600px;
