  font-size: 16px;
  line-height: 1.87;
  letter-spacing: 0.06em;
}
.form .legal-checkbox {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
.form .legal-checkbox:checked + .legal::before {
  background-image: url(../images/icons/icon-check.svg);
  background-size: contain;
  background-origin: border-box;
  border-radius: 4px;
  border-color: transparent;
  background-color: #2196f3;
}

.form-list .form-item {
