/* Portfolio page */
.portfolio .container {
  padding-top: 94px;
  padding-bottom: 94px;
  height: 1554px;
}

.buttons {
  display: flex;
  justify-content: center;
  margin-bottom: 50px;
}
.buttons .item:not(:first-child) {
  margin-left: 8px;
}
.buttons .btn {
  padding: 6px 22px;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.62;
  letter-spacing: 0.03em;
  color: #212121;
  border-radius: 4px;
  border-style: none;
  border-color: #f5f4fa;
  background: #f5f4fa;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1), background-color 250ms cubic-bezier(0.4, 0, 0.2, 1), box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.buttons .btn:focus, .buttons .btn:hover {
  color: #ffffff;
  background-color: #2196f3;
  box-shadow: 0px 3px 1px rgba(0, 0, 0, 0.1), 0px 1px 2px rgba(0, 0, 0, 0.08), 0px 2px 2px rgba(0, 0, 0, 0.12);
  border: none;
  outline: none;
