/* portfolio item list */
.portfolio-list {
  display: flex;
  flex-wrap: wrap;
}
.portfolio-list .portfolio-items:not(:nth-child(3n+1)) {
  margin-left: 30px;
}
.portfolio-list .portfolio-items:not(:nth-last-child(-n+3)) {
  margin-bottom: 30px;
}
.portfolio-list .link {
  position: relative;
  display: block;
  text-decoration: none;
  color: #212121;
  transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
